.dropdown-custom-menu {
    overflow: scroll;
    height: 60vh;
    scrollbar-width: none;
    -ms-overflow-style: none;
}

.dropdown-custom-menu::-webkit-scrollbar {
    display: none;
}

.dropdown-item-container {
    width: 400px;
    display: flex;
    border-radius: 4px;
    justify-content: center;
    align-items: center;
}

.dropdown-item-1 {
    margin: 20px;
}
.dropdown-img {
    width: 120px;
    height: 120px;
}
.info-container {
    width: 100vw;
    height: 100vh;
    background: linear-gradient(100deg, #FF8C00, #FFA500);
}
.info-heading {
    font-size: 54px;
    text-align: center;
    padding-top: 10px;
    color: white;
    font-family: 'Raleway', sans-serif;
}
.info-main {
    margin: auto;
    width: 70%;
}
.info-content {
    color: white;
    font-size: 18px;
}
.developer-info h4 {
    color: white;
    font-family: 'Raleway', sans-serif;
    margin-top: 20px;
}
.info-content a,
.developer-info a {
 color: white;  
}
.usage-container {
    width: 100vw;
    height: 100vh;
    background: linear-gradient(100deg, #FF8C00, #FFA500);
}
.usage-heading {
    font-size: 54px;
    margin-left: 100px;
    padding-top: 50px;
    color: white;
    font-family: 'Raleway', sans-serif;
}

.usage-content {
    color: white;
    font-size: 18px;
    margin: auto;
    margin-left: 100px;
    margin-top: 4px;
}
.usage-content-container {
    width: 70%;
}
body, html {
  margin: 0;
  padding: 0;
}

